<template>
	<view class='details'>
		<!-- 商品图 -->
		<swiper :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000">
			<swiper-item >
				<view class="swiper-item" >
					<image class="swiper-img" :src="goodsContent.imgUrl" mode=""></image>
				</view>
			</swiper-item>
		</swiper>
		<!-- 价格和名称 -->
		<view class="details-goods">
			<view class="goods-pprice">{{goodsContent.pprice}}</view>
			<view class="goods-oprice">{{goodsContent.oprice}}</view>
			<view class="goods-name">{{goodsContent.name}}</view>
		</view>
		<!-- 商品详情 -->
		<view>
			<view><image class='details-img' src="../../static/img/detail1.jpg" mode=""></image></view>
			<view><image class='details-img' src="../../static/img/detail2.jpg" mode=""></image></view>
			<view><image class='details-img' src="../../static/img/detail3.jpg" mode=""></image></view>
			<view><image class='details-img' src="../../static/img/detail4.jpg" mode=""></image></view>
			<view><image class='details-img' src="../../static/img/detail5.jpg" mode=""></image></view>
		</view>
		
		<!-- 商品列表 -->
		<Card cardTitle='看了又看'></Card>
		<CommodityList :dataList='dataList'></CommodityList>
		
		<!-- 底部 -->
		<view class="details-foot">
			<view class='iconfont icon-a31xiaoxi'></view>
			<view class='iconfont icon-gouwuche'></view>
			<view class='add-shopcart' @tap='showPop'>加入购物车</view>
			<view class='purchase' @tap='showPop'>立即购买</view>
		</view>
		
		<!-- 底部弹出层 --><!-- 第57集 -->
		<view class="pop" v-show='isShow' @touchmove.stop=''>
			<!-- 蒙层 -->
			<view class='pop-mask' @tap='hidePop'></view>
			
			
			<!-- 内容块 -->
			<view class="pop-box" :animation="animationData">
				<view>
					<image class='pop-img' src="../../static/img/air.png" mode=""></image>
				</view>
				<view class="pop-num">
					<view>购买数量</view>
					<UniNumberBox>
						min='1'
					</UniNumberBox>
				</view>
				<view class='pop-sub'>
					确定
				</view>
			</view>
		</view>
	</view>
</template>

<script>
import $http from '@/common/api/request.js'
import Card from '../../components/common/Card.vue'
import CommodityList from '../../components/common/CommodityList.vue'
import UniNumberBox from '../../components/uni/uni-number-box/uni-number-box.vue'
	export default {
		data() {
			return {
				isShow:false,
				goodsContent:{},
				animationData:{},
				swiperList:[
					{imgUrl:'../../static/img/details1.jpeg'},
					{imgUrl:'../../static/img/details2.jpeg'},
					{imgUrl:'../../static/img/details3.jpeg'}
				],
				dataList:[
					{
													id:1,
													imgUrl:"../../static/img/hot1.jpg",
													name:"哈密瓜",
													pprice:"15.9",
													oprice:"17.9",
													discount:"5.2"
					},
					{
													id:2,
													imgUrl:"../../static/img/hot2.jpg",
													name:"蓝莓",
													pprice:"69.9",
													oprice:"79.9",
													discount:"5.2"
					},
					{
													id:3,
													imgUrl:"../../static/img/hot3.jpg",
													name:"榴莲",
													pprice:"49.9",
													oprice:"59.9",
													discount:"5.2"
					},
					{
													id:4,
													imgUrl:"../../static/img/hot4.jpg",
													name:"柠檬",
													pprice:"59.9",
													oprice:"69.9",
													discount:"5.2"
					},
					{
													id:5,
													imgUrl:"../../static/img/hot5.jpg",
													name:"上海青",
													pprice:"4.9",
													oprice:"6.9",
													discount:"5.2"
					},
					{
													id:6,
													imgUrl:"../../static/img/hot6.jpg",
													name:"西瓜",
													pprice:"2.9",
													oprice:"4.9",
													discount:"5.2"
					},
					{
													id:7,
													imgUrl:"../../static/img/hot7.jpg",
													name:"香蕉",
													pprice:"3.9",
													oprice:"5.9",
													discount:"5.2"
					},
					{
													id:8,
													imgUrl:"../../static/img/hot8.jpg",
													name:"阳光玫瑰",
													pprice:"15.9",
													oprice:"17.9",
													discount:"5.2"
					},
					{
													id:9,
													imgUrl:"../../static/img/hot9.jpg",
													name:"茄子",
													pprice:"3.5",
													oprice:"6.5",
													discount:"5.2"
					},
				]
			}
		},
		components:{
			Card,
			CommodityList,
			UniNumberBox
		},
		onload(e){
			this.getData(e.id);
		},
		//修改返回的行为,关闭
		onBackPress() {
			if(this.isShow){
				this.hidePop();
				return true;//一定要return
			}
		},
		//点击分享
		onNavigationBarButtonTap(e) {
			if(e.type==='share'){
				uni.share({
					"provider":"weixin",
					"type":"0",
					"scene":"WXSceneSession",
					"title":this.goodsContent.name,
					"href":"http://192.168.10.4:8080/#/pages/details/details?id="+this.goodsContent.id+"",
					"imageUrl":this.goodsContent.imgUrl,
					success: function (res) {
								uni.showTabBar({
									title:"分享成功"
								})
							},
							fail: function (err) {
								console.log("fail:" + JSON.stringify(err));
							}
					
					
					
				})
			}
		},
		methods: {
			//请求数据
			getData(id){
				$http.request({
					url:"/goods/id",
					data:{
						id:id
					}
				}).then((res)=>{
					this.goodsContent=res[0];
				}).catch(()=>{
					uni.showToast({
						title:'请求失败',
						icon:'none'
					})
				})
			},
			showPop(){
			   var animation = uni.createAnimation({
				  duration: 200,
				})
				animation.translateY(600).step();
				this.animation = animation.export();//被清除了
				this.isShow=true;
				setTimeout(()=>{
					//设置出现的效果
					animation.translateY(0).step();
					this.animation = animation.export();
				},200)	
			},
			hidePop(){
				var animation = uni.createAnimation({
				  duration: 200,
				})
				animation.translateY(600).step();
				this.animation = animation.export();//被清除了
				this.isShow=true;
				setTimeout(()=>{
					//设置出现的效果
					animation.translateY(0).step();
					this.isShow=false;
				},200)
				
				
			}
		}
	}
</script>

<style scoped>
swiper{
	width:100%;
	height:700rpx;
}
.details{
	padding-bottom: 90rpx;
}	
.swiper-img{
	width:100%;
	height:700rpx;
}
.details-goods{
	text-align: center;
		font-weight: bold;
		font-size:36rpx;
		padding:10rpx 0;
}
.details-img{
	width: 100%;
}
.details-foot{
	position: fixed;
	left:0;
	bottom: 0;
	width:100%;
	height:90rpx;
	display: flex;
	align-items:center;
	justify-content:center;
	background:#FFFFFF;
}
.details-foot .iconfont{
	width:60rpx;
		height: 60rpx;
		border-radius: 100%;
		background-color: #000000;
		color:#FFFFFF;
		text-align: center;
		margin:0 10rpx;
}
.add-shopcart{
	margin:0 40rpx;
	padding:6rpx 30rpx;
	background-color: #000000;
	color:#FFFFFF;
	border-radius: 40rpx;
}
.purchase{
	margin:0 40rpx;
	padding:6rpx 30rpx;
	background-color: #41a863;
	color:#FFFFFF;
	border-radius: 40rpx;
}
.pop{
	position: fixed;
	left:0;
	top:0;
	width: 100%;
	height: 100%;
	z-index: 9999;
}
.pop-mask{
	position: absolute;
	left:0;
	top:0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.3);
}
.pop-box{
	position: absolute;
	left:0;
	bottom:0;
	width: 100%;
	background-color: #FFFFFF;
}
.pop-img{
	width: 260rpx;
	height: 260rpx;
}
.pop-num{
	padding:20rpx;
	display: flex;
	justify-content: space-between;
}
.pop-sub{
	line-height: 80rpx;
	background-color: #41a863;
	color:#FFFFFF;
	text-align: center;
}
</style>
